<template>
  <div>
    <navPatient/>
    <div class="container bg-light">
      <h1 class="text-center text-dark p-5">
        <b>Registro del Paciente</b>
      </h1>
      <h5 class="text-center text-dark"><b>Llene el siguiente formulario</b></h5>
      <div style="width:400px">
        <b-alert show variant="danger" style="width:400px" v-if="isCorrect!==null">
          <h4 class="alert-heading">{{avise}}</h4>
          <p>{{message}}</p>
        </b-alert>
      </div>

      <form @submit="save">
        <div class="form-row p-5">
          <div class="col-md-6">
            <label for="nombre">Nombres</label>
            <input
              class="form-control"
              placeholder="Ingrese nombres"
              v-model="nombre"
              type="text"
              required
            >
          </div>
          <div class="col-md-6">
            <label for="nombre">Apellidos</label>
            <input
              class="form-control"
              placeholder="Ingrese apellidos"
              v-model="apellido"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <div class="col-md-6">
            <label for="tipoDocumento">Tipo de Documento</label>
            <select
              class="form-control"
              id="tipoDocumento"
              v-model.number="tipoDocumento.id"
              required
            >
              <option disabled value>--Seleccione--</option>
              <option value="1">Pasaporte</option>
              <option value="2">Cedula</option>
            </select>
          </div>
          <div class="col-md-6">
            <label for="numDocumento">Numero de Documento</label>
            <input
              class="form-control"
              placeholder="Numero de documento"
              v-model="numDocumento"
              maxlength="10"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <div class="col-md-6">
            <label for="domicilio">Domicilio</label>
            <input
              class="form-control"
              placeholder="Domicilio"
              v-model="domicilio"
              type="text"
              required
            >
          </div>
          <div class="col-md-6">
            <label for="obraSocial">Obra Social</label>
            <input
              class="form-control"
              placeholder="Obra Social"
              v-model="obraSocial"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <button class="btn btn-primary">Registrar</button>
        </div>
      </form>
    </div>
    <Footer/>
  </div>
</template>
<script>
import { URL, bootstrap, bootbox } from "@/variables.js";
import axios from "axios";
import navPatient from "@/components/patient/navPatient.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "nuevoPaciente",
  data() {
    return {
      tipoDocumento: {
        id: null
      },
      apellido: null,
      nombre: null,
      numDocumento: null,
      domicilio: null,
      obraSocial: null,
      message: null,
      isCorrect: null,
      avise: null,
      medics: []
    };
  },
  methods: {
    save(e) {
      e.preventDefault();
      this.avise = "";
      this.message = "";
      axios
        .post(URL + "/api/paciente", {
          tipoDocumento: this.tipoDocumento,
          apellido: this.apellido,
          nombre: this.nombre,
          numDocumento: this.numDocumento,
          domicilio: this.domicilio,
          obraSocial: this.obraSocial
        })
        .then(response => {
          (this.tipoDocumento = {}), (this.apellido = null);
          this.nombre = null;
          this.numDocumento = null;
          this.domicilio = null;
          this.obraSocial = null;
          bootbox.alert({
            title: "Datos Guardados",
            message: response.data.mensaje,
            locale: "es",
            backdrop: true,
            callback: this.$router.push("/pacientes")
          });
        })
        .catch(error => {
          this.avise = "Error";
          this.isCorrect = false;
          this.message = "Detalle: " + error.response.data.mensaje;
          bootbox.alert({
            title: "Error",
            message: "No se pudo registrar",
            locale: "es",
            backdrop: true
          });
          console.log("Error " + error.response.status + " Conflict");
        });
    }
  },
  components: {
    navPatient,
    Footer
  }
};
</script>
